@import '../../../../assets/scss/base';

.upload_doc {
    @include border-radius (10px);
    background: $grey-color-700;
    // @include flexCenter;
    display: $inline-block;
    span {
        padding: 10px;
        display: $inline-block;
        @include font-size (.70rem);
    }
    .btn-danger {
        cursor: $pointer;
        padding: 10px .85rem;
        background: $error-color;
        border-color: $transparent;
        @include border-radius (0 10px 10px 0);
        box-shadow: $none;
        svg {
            width: 16px;
            display: $inline-block;
            pointer-events: $none;
        }
    }
}